.g-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 45;
  width: 100%;
  height: 100%;
  overflow: scroll;
}

.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
  opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  // overflow-y: auto;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 40;
  transition: 0.3s all;
}

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1),
  opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 44; /*      */
}

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}