.modal-product {
  position: absolute;
  padding: 26px 18px;
  min-width: 280px;
  min-height: 968px;

  background: #ffffff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  border: 2px solid $accent-color;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  // @include mq(desktop, max) {
  //   top: 80px;
  //   left: 50%;
  //   transform: translate(-50%) scale(1);
  // }
  // @include mq(desktop) {
  //   top: 50%;
  //   left: 50%;
  //   transform: translate(-50%, 50%) scale(1);
  // }

  top: 80px;
  left: 50%;
  transform: translate(-50%) scale(1);

  @include mq(tablet) {
    width: 568px;
    padding: 30px 38px;
  }
}

.backdrop {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.2);
  opacity: 1;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-product__button {
  position: absolute;
  top: 12px;
  right: 12px;
  background-color: transparent;
  border: none;
}

.modal-product__icon {
  color: $secondary;
}

.modal-product__icon--big {
  @include mq(tablet, max) {
    display: none;
  }
}

.modal-product__icon--small {
  @include mq(tablet) {
    display: none;
  }
}

.modal-product__name {
  margin-bottom: 20px;

  font-family: 'Seymour One';
  font-weight: 400;
  font-size: 20px;
  line-height: calc(28 / 20);
  color: $secondary;
  text-align: center;
  // text-transform: capitalize;

  @include mq(tablet) {
    margin-bottom: 24px;
  }
}

.modal-product__picture {
  position: relative;
  width: 132px;
  height: 132px;
  margin: 0 auto;
  margin-bottom: 29px;

  border-radius: 50%;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 132px;
    height: 132px;
    transform: translateY(-7px);
    // background-image: url(../images/modal-about_product/img@1x.png);
    background-size: cover;

    // @media (min-device-pixel-ratio: 2),
    //   (min-resolution: 192dpi),
    //   (min-resolution: 2dppx) {
    //   background-image: url(../images/modal-about_product/img@2x.png);
    // transform: translateY(-7px);

    // width: 132px;
    // height: 132px;
    // background-size: cover;
  }

  &--green {
    background: linear-gradient(
        0deg,
        rgba(156, 190, 143, 0.7),
        rgba(156, 190, 143, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);

    &::before {
      background-image: url(../images/modal-about_product/img@1x.png);

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../images/modal-about_product/img@2x.png);
      }
    }
  }

  &--yellow {
    background: linear-gradient(
        0deg,
        rgba(248, 210, 78, 0.7),
        rgba(248, 210, 78, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.26027px 19.726px rgba(153, 114, 23, 0.25);

    &::before {
      background-image: url(../images/modal-about_product/img_2@1x.png);

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../images/modal-about_product/img_2@2x.png);
      }
    }
  }

  &--coral {
    background: linear-gradient(
        0deg,
        rgba(213, 117, 63, 0.7),
        rgba(213, 117, 63, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.26027px 19.726px rgba(0, 0, 0, 0.25);

    &::before {
      background-image: url(../images/modal-about_product/img_3@1x.png);

      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../images/modal-about_product/img_3@2x.png);
      }
    }
  }
}

.modal-components {
  display: flex;
  gap: 9px;
  justify-content: center;
  margin-bottom: 40px;

  @include mq(tablet) {
    gap: 20px;
    margin-bottom: 48px;
  }
}

.modal-components__ellipse {
  position: relative;
  width: 53px;
  height: 72px;
  border-radius: 40px;

  &--green {
    border: 2px solid #9cbe8f;
  }

  &--yellow {
    border: 2px solid #f8d24e;
  }

  &--coral {
    border: 2px solid #d5753f;
  }

  @include mq(tablet) {
    width: 65px;
    height: 90px;
  }
}

.modal-components__circle {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 43px;
  height: 43px;
  border-radius: 50%;

  &--green {
    background: #9cbe8f;
    border: 2px solid #9cbe8f;
    box-shadow: 0px 6px 20px rgba(10, 136, 23, 0.25);
  }

  &--yellow {
    background: #f8d24e;
    border: 2px solid #f8d24e;
    box-shadow: 0px 6px 20px rgba(153, 114, 23, 0.25);
  }

  &--coral {
    background: #d47842;
    border: 2px solid #d5753f;
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25);
  }

  @include mq(tablet) {
    width: 53px;
    height: 53px;
  }
}

.modal-components__number {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;

  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  color: $secondary;
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 10px;
  line-height: calc(28 / 10);

  @include mq(tablet) {
    font-size: 12px;
    line-height: calc(28 / 12);
  }
}

.modal-components__text {
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-bottom: 8px;

  font-family: 'Montserrat';
  font-size: 10px;
  line-height: calc(12 / 10);
  display: flex;
  align-items: center;
  text-align: center;
  color: $secondary;

  transform: translateX(-50%);

  @include mq(tablet) {
    font-size: 12px;
    line-height: calc(15 / 12);
    margin-bottom: 15px;
  }
}

.modal-ingridients {
  margin-bottom: 40px;

  @include mq(tablet) {
    margin-bottom: 48px;
  }
}

.modal-product__title {
  margin-bottom: 24px;

  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 20px;
  line-height: calc(28 / 20);
  color: $secondary;
  text-align: center;
}

.modal-ingridients__item {
  display: flex;
  justify-content: space-between;

  font-family: 'Montserrat';
  font-size: 14px;
  line-height: calc(25 / 14);
  letter-spacing: 0.04em;
  color: $secondary;
}

.modal-ingridients__item:not(:last-child) {
  margin-bottom: 11px;
}

.product-form__input {
  width: 100%;
  height: 42px;
  padding: 11px;
  margin-bottom: 16px;

  background: rgba(248, 210, 78, 0.1);
  border: 1px solid $primary-bgc;
  border-radius: 10px;

  font-size: 14px;
  line-height: calc(20 / 14);
  color: $secondary;

  @include mq(tablet) {
    padding: 11px 16px;
    max-width: 368px;
    display: flex;
    margin-left: auto;
    margin-right: auto;
  }

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: calc(20 / 14);
    color: $secondary;
  }
}

.product-form__comment {
  width: 100%;
  height: 84px;
  padding: 11px;
  margin-bottom: 24px;

  resize: none;
  background: rgba(248, 210, 78, 0.1);
  border: 1px solid $primary-bgc;
  border-radius: 10px;

  font-size: 14px;
  line-height: calc(20 / 14);
  color: $secondary;

  @include mq(tablet) {
    padding: 11px 16px;
    max-width: 368px;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 32px;
  }

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: calc(20 / 14);
    color: $secondary;
  }
}
