.reviews {
  margin-bottom: 490px;
}

.slideshow__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.slideshow__photo {
  display: block;

  width: 85px;
  height: 85px;
  margin-bottom: 32px;

  border-radius: 50%;
}

.slideshow__text {
  display: flex;
  justify-content: center;
  align-items: center;

  max-width: 270px;
  height: 256px;

  margin-bottom: 32px;
  padding: 32px 39px;

  background: hsla(102, 18%, 55%, 1);
  color: $primary-color;
  border-radius: 10px;

  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: calc(31px / 12px);
  text-align: center;
}

.slideshow__name {
  margin: 0;
  max-width: 189px;

  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: calc(37px / 20px);
  text-align: center;

  color: $secondary;
}

@include mq('tablet', 'min') {
  .reviews {
    margin-bottom: 430px;
  }
  .slideshow__text {
    max-width: 705px;
    height: 138px;

    border-radius: 24px;
    padding: 29px 80px;

    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: calc(26px / 14px);
  }

  .slideshow__name {
    max-width: 172px;

    font-weight: 500;
    font-size: 16px;
    line-height: calc(30px / 16px);
  }
}

@include mq('desktop', 'min') {
  .reviews {
    margin-bottom: 260px;
  }

  .slideshow__text {
    max-width: 968px;
    height: 138px;
    border-radius: 24px;
    padding: 43px 109px;

    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: calc(30px / 16px);
  }

  .slideshow__name {
    max-width: 151px;

    font-weight: 500;
    font-size: 16px;
    line-height: calc(30px / 16px);
  }
}

.slick-initialized .slick-slide {
  display: flex;
}

.slick-dots {
  bottom: 0px;
  text-align: center;
  display: flex; //    
  justify-content: center; //   
  list-style: none; //   
}

.slick-dots li button {
  position: absolute;
  top: -15px;
  left: -15px;

  background: #d9d9d9; //  
  border: none; //  
  border-radius: 50%; //  
  height: 15px; //  
  width: 15px; //  
  margin: 18px; //   
  outline: none; //  

  &::before {
    color: transparent;
  }
}

.slick-dots li[class='slick-active'] button {
  background: #84a178;

  &::before {
    color: transparent;
  }
}
